@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@600&family=Open+Sans&family=Poppins&display=swap');

@layer base {
  :root {
    --color-text-base: #ffffff;
    --color-text-muted: #c7d2fe;
    --color-text-inverted: #4f46e5;
    --color-fill: #4338ca;


    --color-button-strong: #0284C7;
    --color-button-light: #38BDF8;
    --color-button-strong-text: #ffffff;
    --color-button-light-text: #ffffff;

  }
}


::-webkit-scrollbar-thumb {
  background-color: #818B99;
  border: 3px solid transparent;
  border-radius: 9px;
  background-clip: content-box;
}

::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

@media print {
  body * {
    visibility: hidden;
  }

  #printableArea,
  #printableArea * {
    visibility: visible;
  }

  #printableArea {
    position: absolute;
    left: 10;
    top: 0;
  }

}